<template>
  <div class="container">
    <div class="card">
      <h1 class="title">QR Code Generator</h1>

      <label class="label" for="urlInput">Link URL</label>
      <input
        id="urlInput"
        v-model="url"
        type="text"
        placeholder="Enter URL"
        class="input"
      />

      <div class="checkbox-group">
        <input type="checkbox" id="includeText" v-model="includeText" />
        <label for="includeText">Include link below QR code</label>
      </div>

      <div class="canvas-wrapper">
        <canvas ref="canvas"></canvas>
      </div>

      <Button @click="exportQR" class="btn btn-danger">
        Export QR Code
      </Button>
    </div>
  </div>
</template>

<script setup>
import { ref, watch, onMounted } from 'vue'
import QRCode from 'qrcode'
import Button from "@/components/Button.vue";

const canvas = ref(null)
const url = ref('https://www.transkon-rent.com/')
const includeText = ref(true)
const logoUrl = 'https://www.transkon-rent.com/uploads/favicon.ico'
const qrSize = 300
const lineHeight = 20
const textPadding = 10

// Helper: load an image as a promise
function loadImage(src) {
  return new Promise((resolve, reject) => {
    const img = new Image()
    img.crossOrigin = 'anonymous'
    img.onload = () => resolve(img)
    img.onerror = reject
    img.src = src
  })
}

// Break text into lines of maxWidth, even if no spaces
function wrapTextToLines(ctx, text, maxWidth) {
  const lines = []
  let line = ''

  for (let char of text) {
    const test = line + char
    if (ctx.measureText(test).width > maxWidth && line !== '') {
      lines.push(line)
      line = char
    } else {
      line = test
    }
  }
  if (line) lines.push(line)
  return lines
}

async function drawQRCode() {
  const ctx = canvas.value.getContext('2d')
  // set font for measurement
  ctx.font = '16px sans-serif'

  // measure how many lines we need (and total text height)
  let lines = []
  let textHeight = 0
  if (includeText.value) {
    lines = wrapTextToLines(ctx, url.value, qrSize - 20)
    textHeight = lines.length * lineHeight + textPadding
  }

  // resize *before* drawing anything
  canvas.value.width = qrSize
  canvas.value.height = qrSize + textHeight
  ctx.clearRect(0, 0, canvas.value.width, canvas.value.height)

  // 1. Render QR onto a temp canvas
  const temp = document.createElement('canvas')
  temp.width = qrSize
  temp.height = qrSize
  await QRCode.toCanvas(temp, url.value, {
    width: qrSize,
    margin: 1,
    color: { dark: '#000000', light: '#FFFFFF' },
  })
  ctx.drawImage(temp, 0, 0)

  // 2. Draw logo on top
  try {
    const logo = await loadImage(logoUrl)
    const logoSize = qrSize * 0.2
    const lx = (qrSize - logoSize) / 2
    const ly = (qrSize - logoSize) / 2
    ctx.fillStyle = '#fff'
    ctx.fillRect(lx, ly, logoSize, logoSize)
    ctx.drawImage(logo, lx, ly, logoSize, logoSize)
  } catch {
    // if logo fails, we just skip it
  }

  // 3. Draw wrapped text below
  if (includeText.value && lines.length) {
    ctx.font = '16px sans-serif'
    ctx.textAlign = 'center'
    ctx.fillStyle = '#333'
    const startY = qrSize + lineHeight
    for (let i = 0; i < lines.length; i++) {
      ctx.fillText(lines[i], qrSize / 2, startY + i * lineHeight)
    }
  }
}

function exportQR() {
  const link = document.createElement('a')

  // strip protocol
  let clean = url.value.replace(/^https?:\/\//, '')

  // — or — replace slashes with double underscores for extra clarity:
  let filenameLink = clean.replace(/\//g, '__')

  // filenameLink = filenameLink.replace(/[^A-Za-z0-9_\-]/g, '')

  const filename = `TRJA-QR-${filenameLink}.png`
  link.download = filename
  link.href = canvas.value.toDataURL('image/png')
  link.click()
}



watch([url, includeText], drawQRCode)
onMounted(drawQRCode)
</script>

<style scoped>
.container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5;
  padding: 20px;
}

.card {
  background: #ffffff;
  padding: 24px 32px;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  max-width: 420px;
  width: 100%;
  text-align: center;
}

.title {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 600;
  color: #333;
}

.label {
  display: block;
  text-align: left;
  margin-bottom: 6px;
  font-weight: 500;
  color: #444;
}

.input {
  width: 100%;
  padding: 10px;
  font-size: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-bottom: 12px;
}

.checkbox-group {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
  font-size: 14px;
  color: #444;
  justify-content: flex-start;
}

.canvas-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.button {
  background-color: #007bff;
  border: none;
  padding: 10px 20px;
  font-size: 15px;
  border-radius: 8px;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #0056b3;
}
</style>
